import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, ReactiveFormsModule } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { CommonModule } from '@angular/common';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-login',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule],
  template: `
    <div class="auth-container">
      <div class="auth-card">
        <h2>SkillForge {{isLogin ? 'Login' : 'Register'}}</h2>
        <div *ngIf="selectedRole" class="role-badge">
          {{isLogin ? 'Login' : 'Register'}} as: {{selectedRole | titlecase}}
        </div>
        <form [formGroup]="authForm" (ngSubmit)="onSubmit()">
          <div class="form-group" *ngIf="!isLogin">
            <input type="text" formControlName="name" placeholder="Full Name" class="form-control">
          </div>
          <div class="form-group">
            <input type="email" formControlName="email" placeholder="Email" class="form-control">
          </div>
          <div class="form-group">
            <input type="password" formControlName="password" placeholder="Password" class="form-control">
          </div>
          <button type="submit" [disabled]="authForm.invalid" class="btn-primary">
            {{isLogin ? 'Login' : 'Register'}} as {{selectedRole | titlecase}}
          </button>
        </form>
        <p>
          <span *ngIf="isLogin">Don't have an account? </span>
          <span *ngIf="!isLogin">Already have an account? </span>
          <a href="#" (click)="toggleMode(); $event.preventDefault()" style="color: #667eea;">
            {{isLogin ? 'Register' : 'Login'}}
          </a>
        </p>
        <p style="text-align: center; margin-top: 15px;">
          <a href="#" (click)="goBack(); $event.preventDefault()" style="color: #667eea;"> Back to Role Selection</a>
        </p>
      </div>
    </div>
  `,
  styles: [`
    .auth-container {
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 100vh;
      padding: 20px;
    }
    .auth-card {
      background: white;
      padding: 40px;
      border-radius: 10px;
      box-shadow: 0 10px 30px rgba(0,0,0,0.1);
      width: 100%;
      max-width: 400px;
    }
    .form-group {
      margin-bottom: 20px;
    }
    .form-control {
      width: 100%;
      padding: 12px;
      border: 1px solid #ddd;
      border-radius: 5px;
      font-size: 16px;
    }
    .btn-primary {
      width: 100%;
      padding: 12px;
      background: #667eea;
      color: white;
      border: none;
      border-radius: 5px;
      font-size: 16px;
      cursor: pointer;
    }
    .btn-primary:disabled {
      background: #ccc;
    }
    .role-badge {
      background: #667eea;
      color: white;
      padding: 8px 16px;
      border-radius: 20px;
      text-align: center;
      margin-bottom: 20px;
      font-size: 14px;
    }
  `]
})
export class LoginComponent implements OnInit {
  authForm: FormGroup;
  selectedRole: string = '';
  isLogin: boolean = true;

  constructor(
    private fb: FormBuilder,
    private authService: AuthService,
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.authForm = this.fb.group({
      name: [''],
      email: ['', [Validators.required, Validators.email]],
      password: ['', Validators.required]
    });
  }

  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      this.selectedRole = params['role'] || localStorage.getItem('selectedRole') || '';
      this.isLogin = params['mode'] !== 'register';
    });
  }

  toggleMode() {
    this.isLogin = !this.isLogin;
    if (!this.isLogin) {
      this.authForm.get('name')?.setValidators([Validators.required]);
    } else {
      this.authForm.get('name')?.clearValidators();
    }
    this.authForm.get('name')?.updateValueAndValidity();
  }

  onSubmit() {
    if (this.authForm.valid) {
      const formData = this.authForm.value;
      
      if (this.isLogin) {
        this.authService.login(formData).subscribe({
          next: (response) => {
            localStorage.setItem('token', response.token);
            localStorage.setItem('userRole', response.role);
            this.redirectBasedOnRole(response.role);
          },
          error: (error) => console.error('Login failed', error)
        });
      } else {
        const registerData = {
          ...formData,
          role: this.selectedRole.toUpperCase()
        };
        this.authService.register(registerData).subscribe({
          next: (response) => {
            localStorage.setItem('token', response.token);
            localStorage.setItem('userRole', response.role);
            this.redirectBasedOnRole(response.role);
          },
          error: (error) => console.error('Registration failed', error)
        });
      }
    }
  }

  goBack() {
    this.router.navigate(['/']);
  }

  redirectBasedOnRole(role: string) {
    switch(role) {
      case 'STUDENT':
        this.router.navigate(['/dashboard/student']);
        break;
      case 'INSTRUCTOR':
        this.router.navigate(['/dashboard/instructor']);
        break;
      case 'ADMIN':
        this.router.navigate(['/dashboard/admin']);
        break;
      default:
        this.router.navigate(['/dashboard']);
    }
  }
}